





















import {
  Component, Prop, Vue, Watch,
} from 'vue-property-decorator';
import { DonationPoint, DonationPointPosition } from '@/models/DonationPoint';
import DonationPointList from '@/components/DonationPointList.vue';
import DonationPointsMap from '@/components/DonationPointsMap.vue';
import DonationPointItem from '@/components/DonationPointItem.vue';
import DonationPointItemSelected from '@/components/DonationPointItemSelected.vue';

@Component({
  components: {
    DonationPointItem,
    DonationPointsMap,
    DonationPointList,
    DonationPointItemSelected,
  },
})
export default class Locations extends Vue {
  @Prop() rawPoints!: DonationPoint[];

  @Prop() userPosition?: DonationPointPosition;

  @Watch('userPosition', { immediate: true, deep: true })
  userPositionChanged(newUserPosition?: DonationPointPosition) {
    if (newUserPosition) {
      // eslint-disable-next-line prefer-destructuring
      this.selectedPoint = this.points[0];
    }
  }

  get points(): DonationPoint[] {
    const disabledMaps = ['9'];
    return this.rawPoints
      // filter disabled maps
      .filter((p) => !disabledMaps.some((dm) => p.mapId === dm))
      // sort based on user location
      .sort(this.sortPointsByUserLocation)
      // add id
      .map((p, i) => ({ id: i + 1, ...p }));
  }

  selectedPoint: DonationPoint = this.points[0];

  get sortedPointsLessSelected() {
    return this.points.filter((p) => p.id !== this.selectedPoint.id);
  }

  setSelectedPoint(point: DonationPoint) {
    this.selectedPoint = point;
  }

  sortPointsByUserLocation(a: DonationPoint, b: DonationPoint): number {
    let userLat = 0;
    let userLng = 0;
    if (this.userPosition) {
      userLat = this.userPosition.lat;
      userLng = this.userPosition.lng;
    }

    const distB = this.distance(b.position.lat, b.position.lng, userLat, userLng);
    const distA = this.distance(a.position.lat, a.position.lng, userLat, userLng);

    let diff = 0;
    if (distA > distB) {
      diff = 1;
    } if (distA < distB) {
      diff = -1;
    }
    return diff;
  }

  // eslint-disable-next-line class-methods-use-this
  distance(lat1: number, lon1: number, lat2: number, lon2: number, unit = 'K') {
    const radLat1 = Math.PI * (lat1 / 180);
    const radLat2 = Math.PI * (lat2 / 180);
    const theta = lon1 - lon2;
    const radTheta = Math.PI * (theta / 180);
    let dist = Math.sin(radLat1) * Math.sin(radLat2)
      + Math.cos(radLat1) * Math.cos(radLat2) * Math.cos(radTheta);
    if (dist > 1) {
      dist = 1;
    }
    dist = Math.acos(dist);
    dist *= (180 / Math.PI);
    dist = dist * 60 * 1.1515;
    if (unit === 'K') { dist *= 1.609344; }
    if (unit === 'N') { dist *= 0.8684; }
    return dist;
  }
}
