
















import { Component, Vue } from 'vue-property-decorator';
import { DonationPoint, DonationPointPosition } from '@/models/DonationPoint';
import { RemoteData } from '@/models/RemoteData';
import DonationPointsService from '@/services/DonationPointsService';
import Locations from '@/components/Locations.vue';
import FindLocation from '@/views/FindLocation.vue';

@Component({
  components: {
    Locations,
    FindLocation,
  },
})
export default class LocationsLoading extends Vue {
  locations: RemoteData<Error, DonationPoint[]> = {
    type: 'NOT_ASKED',
  };

  userPosition?: DonationPointPosition;

  // eslint-disable-next-line class-methods-use-this
  data(): object {
    return {
      userPosition: null,
    };
  }

  created() {
    this.locations = { type: 'LOADING' };
    DonationPointsService.get().then((remoteData) => { this.locations = remoteData; });
  }

  setUserPosition(userPosition: DonationPointPosition) {
    this.userPosition = userPosition;
  }
}
