import { DonationPoint, DonationPointPosition } from '@/models/DonationPoint';
import { RemoteData } from '@/models/RemoteData';

export default class DonationPointsService {
  static async get(): Promise<RemoteData<Error, DonationPoint[]>> {
    try {
      const response = await fetch('https://uniaoaudiovisual.pt/wp-json/wpgmza/v1/markers');

      if (!response.ok) throw await response.json();

      const rawData = await response.json();
      const data = this.decodeMarkers(rawData);
      return { type: 'SUCCESS', data };
    } catch (e) {
      return { type: 'FAILURE', error: e };
    }
  }

  // eslint-disable-next-line class-methods-use-this
  private static decodeMarkers(rawData: unknown): DonationPoint[] {
    let result: DonationPoint[] = [];
    if (rawData instanceof Array) {
      result = rawData.map((raw: any) => ({
        mapId: raw?.map_id,
        description: raw?.description,
        title: raw?.title,
        position: {
          lat: parseFloat(raw?.lat),
          lng: parseFloat(raw?.lng),
        },
      }));
    }
    return result;
  }
}
