

















import {
  Component, Emit, Prop, Vue,
} from 'vue-property-decorator';
import { DonationPoint, DonationPointPosition } from '@/models/DonationPoint';

@Component({})
export default class DonationPointItemSelected extends Vue {
    // needed because this.$sanitize and typescript
    [x: string]: any;

    @Prop() private point!: DonationPoint;

    @Prop() private userPosition?: DonationPointPosition;

    get cleanDescription() {
      return this.$sanitize(this.point.description);
    }

    get googleMapsUrl() {
      const destination = `${this.point.position.lat},${this.point.position.lng}`;
      const origin = this.userPosition
        ? `${this.userPosition.lat},${this.userPosition.lng}`
        : '';
      return this.userPosition
        ? `https://www.google.com/maps/dir/?api=1&origin=${origin}&destination=${destination}`
        : `https://www.google.com/maps/dir/@?api=1&map_action=map&center=${destination}`;
    }
}
